.work-experience-card {
    background: #ebf1fb;
    color: black;
    border-radius: 0.65rem;
    border: 1.5px solid #d1cece;
    padding: 1.5rem;
    margin: 0 1rem;
    user-select: text; /* 允许用户选择文本 */
}

.work-experience-card h6 {
    font-size: 1.5rem;
    color: black;
    font-weight: 500;
    margin-bottom: 0.7rem;
}

.work-duration {
    display: inline-block;
    font-size: 0.7rem;
    font-weight: 400;
    color: black;
    background: rgba(146, 173, 255, 0.5);
    border-radius: 0.3rem;
    padding: 0.4rem 0.6rem;
    margin-bottom: 1.3rem;
}


.work-experience-card ul li {
    list-style: none;
    font-size: 1.0rem;
    font-weight: 400;
    margin-bottom: 0.5rem;
    position: relative;
}

.work-experience-card ul li::after {
    content: " ";
    width: 0.5rem;
    height: 0.5rem;
    background-color: black;
    border-radius: 0.5rem;
    position: absolute;
    left: -1.3rem;
    top: 0.35rem;
}

@media (max-width: 768px) {
    .work-experience-card {
        margin: 0;
    }

    .work-experience-card h6 {
        font-size: 0.95rem;
    }

    .work-experience-card ul li {
        font-size: 0.75rem;
    }

}